"use client";

import React from 'react';
import { TicketMensagem } from '@/services/api/ticket.service';
import { formatDate } from '@/utils/format';
import { useAuth } from '@/hooks/useAuth';
import { getServerBaseURL } from '@/services/api/config';
import Image from 'next/image';
import { getAppConfig } from '@/hooks/useAppConfig';

interface TicketMensagemCardProps {
  mensagem: TicketMensagem;
}

function formatTimeAgo(dateString: string): string {
  try {
    const date = new Date(dateString);
    const now = new Date();
    const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);

    if (diffInSeconds < 60) return 'agora';
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} min atrás`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hr atrás`;
    
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    const messageDate = new Date(date);
    messageDate.setHours(0, 0, 0, 0);
    
    if (messageDate.getTime() === today.getTime()) {
      return `Hoje às ${date.toLocaleTimeString('pt-BR', { hour: '2-digit', minute: '2-digit' })}`;
    }
    
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);
    if (messageDate.getTime() === yesterday.getTime()) {
      return `Ontem às ${date.toLocaleTimeString('pt-BR', { hour: '2-digit', minute: '2-digit' })}`;
    }
    
    return date.toLocaleDateString('pt-BR', { 
      day: '2-digit', 
      month: '2-digit', 
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  } catch {
    return dateString;
  }
}

export default function TicketMensagemCard({
  mensagem
}: TicketMensagemCardProps) {
  const { user } = useAuth();
  const isOwnMessage = mensagem.userId === user?.id;
  const isAdmin = mensagem.user?.role === 'SUPERADMIN';

  return (
    <div className={`flex ${isOwnMessage ? 'justify-end' : 'justify-start'} mb-4`}>
      <div className={`flex flex-col max-w-[70%] ${isOwnMessage ? 'items-end' : 'items-start'}`}>
        {/* Header com nome e badge */}
        <div className={`flex items-center gap-2 mb-1 ${isOwnMessage ? 'flex-row-reverse' : ''}`}>
          <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">
            {mensagem.user?.nome || 'Usuário'}
          </span>
          {isAdmin && (
            <span className="text-xs px-2 py-0.5 rounded-full bg-purple-100 dark:bg-purple-900/50 text-purple-700 dark:text-purple-300 font-medium">
              ADMIN
            </span>
          )}
          <span className="text-xs text-gray-500 dark:text-gray-400">
            {formatTimeAgo(mensagem.createdAt)}
          </span>
        </div>
        
        {/* Anexos */}
        {mensagem.anexos && Array.isArray(mensagem.anexos) && mensagem.anexos.length > 0 && (
          <div className={`mt-2 ${isOwnMessage ? 'text-right' : 'text-left'}`}>
            <div className="flex flex-wrap gap-2">
              {(mensagem.anexos as any[]).map((anexo: any, index: number) => {
                const isImage = anexo.tipo?.startsWith('image/');
                const config = getAppConfig();
                const fileUrl = anexo.url ? `${config?.backendUrl}${anexo.url}` : '#';
                console.log('fileUrl:', fileUrl);
                return (
                  <div key={index} className="flex flex-col gap-1">
                    {isImage ? (
                      <a
                        href={fileUrl}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="block"
                      >
                        <Image
                          src={fileUrl}
                          alt={anexo.nome || `Anexo ${index + 1}`}
                          className="max-w-[200px] max-h-[200px] rounded-lg object-cover border border-gray-200 dark:border-gray-600 hover:opacity-80 transition-opacity cursor-pointer"
                          width={200}
                          height={200}
                        />
                      </a>
                    ) : (
                      <a
                        href={fileUrl}
                        target="_blank"
                        rel="noopener noreferrer"
                        className={`inline-flex items-center gap-1 text-xs px-3 py-1.5 rounded-lg transition-colors ${
                          isOwnMessage
                            ? 'bg-blue-400/20 text-blue-100 hover:bg-blue-400/30'
                            : 'bg-gray-100 dark:bg-gray-600 text-gray-700 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-500'
                        }`}
                      >
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.172 7l-6.586 6.586a2 2 0 102.828 2.828l6.414-6.586a4 4 0 00-5.656-5.656l-6.415 6.585a6 6 0 108.486 8.486L20.5 13" />
                        </svg>
                        {anexo.nome || `Anexo ${index + 1}`}
                      </a>
                    )}
                    {isImage && (
                      <span className="text-xs text-gray-500 dark:text-gray-400 truncate max-w-[200px]">
                        {anexo.nome || `Anexo ${index + 1}`}
                      </span>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {/* Mensagem */}
        <div
          className={`rounded-2xl px-4 py-3 shadow-sm ${
            isOwnMessage
              ? 'bg-blue-500 text-white rounded-tr-sm'
              : isAdmin
              ? 'bg-purple-50 dark:bg-purple-900/20 border border-purple-200 dark:border-purple-800 text-gray-900 dark:text-white rounded-tl-sm'
              : 'bg-white dark:bg-gray-700 border border-gray-200 dark:border-gray-600 text-gray-900 dark:text-white rounded-tl-sm'
          }`}
        >
          <p className="text-sm leading-relaxed whitespace-pre-wrap break-words">
            {mensagem.mensagem}
          </p>
        </div>

      </div>
    </div>
  );
}
