import { useEffect, useState, useCallback, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { io, Socket } from 'socket.io-client';
import { services } from '@/services/api';
import type { Ticket, TicketMensagem } from '@/services/api/ticket.service';
import { cookies } from '@/utils/cookies';
import toast from 'react-hot-toast';
import { useAuth } from './useAuth';
import { useBackendUrl } from './useAppConfig';

interface UseTicketsReturn {
  tickets: Ticket[];
  loading: boolean;
  isConnected: boolean;
  currentTicketId: number | null;
  loadTickets: (params?: any) => Promise<void>;
  loadTicket: (id: number) => Promise<Ticket | null>;
  joinTicketRoom: (ticketId: number) => void;
  leaveTicketRoom: (ticketId: number) => void;
}

export function useTickets(): UseTicketsReturn {
  const router = useRouter();
  const { user, isAuthenticated } = useAuth();
  const backendUrl = useBackendUrl();
  const [tickets, setTickets] = useState<Ticket[]>([]);
  const [loading, setLoading] = useState(true);
  const [socket, setSocket] = useState<Socket | null>(null);
  const [isConnected, setIsConnected] = useState(false);
  const [currentTicketId, setCurrentTicketId] = useState<number | null>(null);
  const socketInitializedRef = useRef(false);

  // Inicializar Socket.IO apenas quando autenticado
  useEffect(() => {
    const token = cookies.token.get();
    if (!token || !isAuthenticated || !user) {
      setSocket(prevSocket => {
        if (prevSocket) {
          prevSocket.close();
          setIsConnected(false);
        }
        return null;
      });
      socketInitializedRef.current = false;
      return;
    }

    if (socketInitializedRef.current) {
      return;
    }

    // Usar backendUrl diretamente
    if (!backendUrl) {
      console.warn('[Socket.IO] backendUrl não configurada');
      return;
    }
    
    // Evitar múltiplas inicializações
    if (socketInitializedRef.current) {
      return;
    }
    
    socketInitializedRef.current = true;
    
    console.log('[Socket.IO] Inicializando conexão com:', backendUrl);
    
    const newSocket = io(backendUrl, {
      auth: {
        token: token
      },
      transports: ['websocket', 'polling'],
      reconnection: true,
      reconnectionDelay: 2000, // Aumentado para evitar reconexões muito rápidas
      reconnectionAttempts: 5,
      reconnectionDelayMax: 5000,
      timeout: 20000,
      forceNew: false // Reutilizar conexão existente se possível
    });

    newSocket.on('connect', () => {
      console.log('[Socket.IO Tickets] Conectado');
      setIsConnected(true);
    });

    newSocket.on('disconnect', () => {
      console.log('[Socket.IO Tickets] Desconectado');
      setIsConnected(false);
    });

    newSocket.on('connect_error', (error) => {
      console.error('[Socket.IO Tickets] Erro de conexão:', error);
      setIsConnected(false);
    });

    // Escutar nova mensagem em ticket
    newSocket.on('ticket:nova-mensagem', (data: { ticketId: number; ticket?: Ticket; mensagem: TicketMensagem }) => {
      console.log('[Socket.IO Tickets] Nova mensagem recebida:', data);
      
      // Se não estiver visualizando o ticket, mostrar toast
      if (currentTicketId !== data.ticketId) {
        const ticketTitle = data.ticket?.titulo || 'Ticket';
        toast.success(`Nova mensagem no ticket: ${ticketTitle}`, {
          duration: 5000,
        });
      }
      // A atualização do ticket será feita diretamente nas páginas de detalhes
    });

    // Escutar atualização de status
    newSocket.on('ticket:status-atualizado', (data: { ticketId: number; status: string }) => {
      console.log('[Socket.IO Tickets] Status atualizado:', data);
      
      setTickets(prev => {
        const index = prev.findIndex(t => t.id === data.ticketId);
        if (index >= 0) {
          const updated = [...prev];
          updated[index] = { ...updated[index], status: data.status as any };
          return updated;
        }
        return prev;
      });
    });

    setSocket(newSocket);

    return () => {
      if (newSocket) {
        newSocket.close();
      }
      socketInitializedRef.current = false;
    };
  }, [isAuthenticated, user, backendUrl]); // Removido currentTicketId para evitar loop

  const loadTickets = useCallback(async (params?: any) => {
    try {
      setLoading(true);
      const response = await services.ticket.list(params);
      setTickets(response.data);
    } catch (error: any) {
      console.error('Erro ao carregar tickets:', error);
      toast.error('Erro ao carregar tickets');
    } finally {
      setLoading(false);
    }
  }, []);

  const loadTicket = useCallback(async (id: number): Promise<Ticket | null> => {
    try {
      const response = await services.ticket.getById(id);
      return response.data;
    } catch (error: any) {
      console.error('Erro ao carregar ticket:', error);
      toast.error('Erro ao carregar ticket');
      return null;
    }
  }, []);

  const joinTicketRoom = useCallback((ticketId: number) => {
    if (socket && isConnected) {
      socket.emit('ticket:join', { ticketId });
      setCurrentTicketId(ticketId);
    }
  }, [socket, isConnected]);

  const leaveTicketRoom = useCallback((ticketId: number) => {
    if (socket && isConnected) {
      socket.emit('ticket:leave', { ticketId });
      if (currentTicketId === ticketId) {
        setCurrentTicketId(null);
      }
    }
  }, [socket, isConnected, currentTicketId]);

  return {
    tickets,
    loading,
    isConnected,
    currentTicketId,
    loadTickets,
    loadTicket,
    joinTicketRoom,
    leaveTicketRoom
  };
}

