'use client';

import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useRouter, useParams } from 'next/navigation';
import { toast } from 'react-hot-toast';
import { Card } from '@/components/ui/card';
import Button from '@/components/ui/button/Button';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Select, { SelectOption } from '@/components/ui/Select';
import { getApiBaseUrl, services } from '@/services/api';
import type { Ticket, TicketMensagem, StatusTicket } from '@/services/api/ticket.service';
import TicketMensagemCard from '@/components/tickets/TicketMensagemCard';
import Badge from '@/components/ui/badge/Badge';
import { formatDate } from '@/utils/format';
import { ChevronLeftIcon } from '@/icons';
import { useTickets } from '@/hooks/useTickets';
import { useAuth } from '@/hooks/useAuth';
import { useBackendUrl } from '@/hooks/useAppConfig';

function getStatusColor(status: string): 'success' | 'warning' | 'error' | 'info' {
  switch (status) {
    case 'ABERTO':
      return 'info';
    case 'EM_ANDAMENTO':
      return 'warning';
    case 'RESOLVIDO':
      return 'success';
    case 'FECHADO':
      return 'error';
    default:
      return 'info';
  }
}

function getStatusLabel(status: string): string {
  switch (status) {
    case 'ABERTO':
      return 'Aberto';
    case 'EM_ANDAMENTO':
      return 'Em Andamento';
    case 'RESOLVIDO':
      return 'Resolvido';
    case 'FECHADO':
      return 'Fechado';
    default:
      return status;
  }
}

function getTipoLabel(tipo: string): string {
  switch (tipo) {
    case 'PROBLEMA':
      return 'Problema';
    case 'AJUDA':
      return 'Ajuda';
    case 'MELHORIA':
      return 'Melhoria';
    case 'SUGESTAO':
      return 'Sugestão';
    default:
      return tipo;
  }
}

function getPrioridadeLabel(prioridade: string): string {
  switch (prioridade) {
    case 'BAIXA':
      return 'Baixa';
    case 'MEDIA':
      return 'Média';
    case 'ALTA':
      return 'Alta';
    case 'URGENTE':
      return 'Urgente';
    default:
      return prioridade;
  }
}

export default function TicketDetailPage() {
  const router = useRouter();
  const params = useParams();
  const { user } = useAuth();
  const { loadTicket, joinTicketRoom, leaveTicketRoom } = useTickets();
  const backendUrl = useBackendUrl();
  const [ticket, setTicket] = useState<Ticket | null>(null);
  const [loading, setLoading] = useState(true);
  const [mensagem, setMensagem] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [selectedFiles, setSelectedFiles] = useState<File[]>([]);
  const [status, setStatus] = useState<'ABERTO' | 'EM_ANDAMENTO' | 'RESOLVIDO' | 'FECHADO'>('ABERTO');
  const [isUpdatingStatus, setIsUpdatingStatus] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const socketRef = useRef<any>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const ticketId = parseInt(params.id as string);

  const loadTicketData = useCallback(async () => {
    try {
      setLoading(true);
      const data = await loadTicket(ticketId);
      if (data) {
        setTicket(data);
      } else {
        toast.error('Ticket não encontrado');
        router.push('/dashboard/empresa/tickets');
      }
    } catch (error) {
      console.error('Erro ao carregar ticket:', error);
      toast.error('Erro ao carregar ticket');
      router.push('/dashboard/empresa/tickets');
    } finally {
      setLoading(false);
    }
  }, [loadTicket, ticketId, router]);

  useEffect(() => {
    if (ticketId) {
      loadTicketData();
      joinTicketRoom(ticketId);
      
      // Configurar listener Socket.IO para atualizações em tempo real
      const setupSocket = async () => {
        const { cookies } = await import('@/utils/cookies');
        const token = cookies.token.get();
        
        if (backendUrl && typeof window !== 'undefined' && token) {
          const { io } = await import('socket.io-client');
          const socket = io(backendUrl, {
            auth: { token },
            transports: ['websocket', 'polling']
          });

          socket.on('ticket:nova-mensagem', (data: { ticketId: number; ticket?: Ticket; mensagem: any }) => {
            if (data.ticketId === ticketId && data.ticket) {
              setTicket(data.ticket);
            }
          });

          socket.on('ticket:status-atualizado', (data: { ticketId: number; status: string }) => {
            if (data.ticketId === ticketId) {
              setTicket(prev => prev ? { ...prev, status: data.status as any } : null);
            }
          });

          socketRef.current = socket;
        }
      };
      
      setupSocket();
      
      return () => {
        leaveTicketRoom(ticketId);
        if (socketRef.current) {
          socketRef.current.close();
        }
      };
    }
  }, [ticketId, loadTicketData, joinTicketRoom, leaveTicketRoom, backendUrl]);

  // Scroll para última mensagem quando novas mensagens chegarem
  useEffect(() => {
    if (ticket?.mensagens && ticket.mensagens.length > 0) {
      messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }
  }, [ticket?.mensagens]);

  // Inicializar status quando ticket carregar
  useEffect(() => {
    if (ticket) {
      setStatus(ticket.status as StatusTicket);
    }
  }, [ticket]);

  const handleSubmitMensagem = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!mensagem.trim() && selectedFiles.length === 0) {
      return;
    }

    setIsSubmitting(true);
    try {
      const mensagemData = mensagem.trim() || (selectedFiles.length > 0 ? 'Anexos enviados' : '');
      await services.ticket.addMensagem(
        ticketId,
        {
          mensagem: mensagemData
        },
        selectedFiles.length > 0 ? selectedFiles : undefined
      );
      setMensagem('');
      setSelectedFiles([]);
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
      toast.success('Mensagem enviada com sucesso');
      await loadTicketData();
    } catch (error: any) {
      toast.error(error?.response?.data?.error || 'Erro ao enviar mensagem');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = Array.from(e.target.files);
      if (files.length > 5) {
        toast.error('Máximo de 5 arquivos permitidos');
        return;
      }
      setSelectedFiles(files);
    }
  };

  const removeFile = (index: number) => {
    setSelectedFiles(prev => prev.filter((_, i) => i !== index));
  };

  const handleUpdateStatus = async () => {
    if (!ticket || ticket.status === status) {
      return;
    }

    setIsUpdatingStatus(true);
    try {
      await services.ticket.update(ticket.id, {
        status
      });
      toast.success('Status do ticket atualizado com sucesso');
      await loadTicketData();
    } catch (error: any) {
      toast.error(error?.response?.data?.error || 'Erro ao atualizar status do ticket');
    } finally {
      setIsUpdatingStatus(false);
    }
  };

  const isCreator = user?.id === ticket?.criadorId;
  const statusOptions: SelectOption[] = [
    { value: 'ABERTO', label: 'Aberto' },
    { value: 'EM_ANDAMENTO', label: 'Em Andamento' },
    { value: 'RESOLVIDO', label: 'Resolvido' },
    { value: 'FECHADO', label: 'Fechado' }
  ];

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <p className="text-gray-500 dark:text-gray-400">Carregando ticket...</p>
      </div>
    );
  }

  if (!ticket) {
    return null;
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4">
        <Button
          variant="outline"
          onClick={() => router.push('/dashboard/empresa/tickets')}
          startIcon={<ChevronLeftIcon className="w-5 h-5" />}
          aria-label="Voltar para lista de tickets"
        >
          Voltar
        </Button>
        <div className="flex-1">
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
            {ticket.titulo}
          </h1>
          <div className="flex items-center gap-2 mt-2 flex-wrap">
            <Badge color={getStatusColor(ticket.status)}>
              {getStatusLabel(ticket.status)}
            </Badge>
            <Badge color="info">
              {getTipoLabel(ticket.tipo)}
            </Badge>
            <Badge color="warning">
              {getPrioridadeLabel(ticket.prioridade)}
            </Badge>
          </div>
        </div>
      </div>

      {/* Informações do Ticket */}
      <Card className="p-4">
        <div className="space-y-2">
          <div>
            <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
              Descrição:
            </span>
            <p className="text-sm text-gray-600 dark:text-gray-400 mt-1 whitespace-pre-wrap">
              {ticket.descricao}
            </p>
          </div>
          
          {/* Controles de Status (apenas para o criador) */}
          {isCreator && (
            <div className="pt-4 border-t border-gray-200 dark:border-gray-700">
              <Label htmlFor="status">Status do Ticket</Label>
              <div className="flex items-center gap-3 mt-2">
                <Select
                  id="status"
                  options={statusOptions}
                  value={statusOptions.find(opt => opt.value === status)}
                  onChange={(selectedOption: any) => setStatus((selectedOption as any)?.value || 'ABERTO')}
                  isSearchable={false}
                  className="flex-1"
                  aria-label="Status do ticket"
                />
                <Button
                  type="button"
                  variant="primary"
                  onClick={handleUpdateStatus}
                  disabled={isUpdatingStatus || ticket.status === status}
                  aria-label="Atualizar status"
                >
                  {isUpdatingStatus ? 'Atualizando...' : 'Atualizar Status'}
                </Button>
              </div>
            </div>
          )}
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 pt-4 border-t border-gray-200 dark:border-gray-700">
            <div>
              <span className="text-xs text-gray-500 dark:text-gray-400">Criado por:</span>
              <p className="text-sm text-gray-700 dark:text-gray-300">
                {ticket.criador?.nome || 'N/A'}
              </p>
            </div>
            <div>
              <span className="text-xs text-gray-500 dark:text-gray-400">Data de criação:</span>
              <p className="text-sm text-gray-700 dark:text-gray-300">
                {formatDate(ticket.createdAt)}
              </p>
            </div>
          </div>
        </div>
      </Card>

      {/* Mensagens - Chat Moderno */}
      <Card className="p-0 overflow-hidden">
        {/* Header do Chat */}
        <div className="px-6 py-4 border-b border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-800/50">
          <h2 className="text-lg font-semibold text-gray-900 dark:text-white">
            Mensagens ({ticket.mensagens?.length || 0})
          </h2>
        </div>
        
        {/* Área de Mensagens */}
        <div className="px-6 py-4 max-h-[600px] min-h-[400px] overflow-y-auto bg-gray-50 dark:bg-gray-900/50">
          {ticket.mensagens && ticket.mensagens.length > 0 ? (
            <>
              {ticket.mensagens.map((mensagem) => (
                <TicketMensagemCard
                  key={mensagem.id}
                  mensagem={mensagem}
                />
              ))}
              <div ref={messagesEndRef} />
            </>
          ) : (
            <div className="flex items-center justify-center h-full min-h-[300px]">
              <div className="text-center">
                <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gray-200 dark:bg-gray-700 flex items-center justify-center">
                  <svg className="w-8 h-8 text-gray-400 dark:text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                  </svg>
                </div>
                <p className="text-sm text-gray-500 dark:text-gray-400">
                  Nenhuma mensagem ainda. Seja o primeiro a comentar!
                </p>
              </div>
            </div>
          )}
        </div>

        {/* Formulário de Nova Mensagem */}
        <div className="px-6 py-4 border-t border-gray-200 dark:border-gray-700 bg-white dark:bg-gray-800">
          <form onSubmit={handleSubmitMensagem} className="space-y-3">
            {/* Preview de arquivos selecionados */}
            {selectedFiles.length > 0 && (
              <div className="flex flex-wrap gap-2">
                {selectedFiles.map((file, index) => (
                  <div key={index} className="flex items-center gap-2 px-3 py-1.5 bg-gray-100 dark:bg-gray-700 rounded-lg text-sm">
                    <span className="text-gray-700 dark:text-gray-300 truncate max-w-[200px]">
                      {file.name} ({(file.size / 1024 / 1024).toFixed(2)} MB)
                    </span>
                    <button
                      type="button"
                      onClick={() => removeFile(index)}
                      className="text-red-600 hover:text-red-700 dark:text-red-400 dark:hover:text-red-300 ml-1"
                      aria-label={`Remover ${file.name}`}
                    >
                      ✕
                    </button>
                  </div>
                ))}
              </div>
            )}
            
            <div className="flex items-center gap-3">
              <div className="flex-1">
                <textarea
                  id="mensagem"
                  value={mensagem}
                  onChange={(e) => setMensagem(e.target.value)}
                  rows={3}
                  className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-xl shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:text-white resize-none"
                  placeholder="Digite sua mensagem..."
                  aria-label="Mensagem do ticket"
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !e.shiftKey) {
                      e.preventDefault();
                      if ((mensagem.trim() || selectedFiles.length > 0) && !isSubmitting) {
                        handleSubmitMensagem(e as any);
                      }
                    }
                  }}
                />
              </div>
              
              <div className="flex flex-col gap-2">
                <input
                  ref={fileInputRef}
                  type="file"
                  multiple
                  onChange={handleFileChange}
                  accept="image/*,application/pdf,.doc,.docx,.xls,.xlsx,.txt,.csv"
                  className="hidden"
                  aria-label="Anexar arquivos"
                  id="file-input-ticket"
                />
                <Button
                  type="button"
                  variant="outline"
                  className="h-full flex items-center justify-center"
                  aria-label="Anexar arquivo"
                  onClick={() => fileInputRef.current?.click()}
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.172 7l-6.586 6.586a2 2 0 102.828 2.828l6.414-6.586a4 4 0 00-5.656-5.656l-6.415 6.585a6 6 0 108.486 8.486L20.5 13" />
                  </svg>
                </Button>
                
                <Button
                  type="submit"
                  variant="primary"
                  disabled={isSubmitting || (!mensagem.trim() && selectedFiles.length === 0)}
                  className="h-full flex items-center justify-center"
                  aria-label="Enviar mensagem"
                >
                  {isSubmitting ? (
                    <svg className="animate-spin h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                  ) : (
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
                    </svg>
                  )}
                </Button>
              </div>
            </div>
            
            <p className="text-xs text-gray-500 dark:text-gray-400">
              Máximo 5 arquivos, 10MB cada. Formatos: imagens, PDFs, documentos do Office, textos.
            </p>
          </form>
        </div>
      </Card>
    </div>
  );
}

